import {
  AfterViewInit, Directive, DoCheck, ElementRef, EventEmitter, Input,
  NgZone, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, Optional
} from '@angular/core';
// import { init } from 'echarts';
import { fromEvent, Observable, Subscription } from 'rxjs';
import { debounceTime, switchMap } from 'rxjs/operators';
import { ChangeFilter } from './change.filter';

declare var echarts: any;

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: 'qdp-charts, [qdp-charts]',
})
export class NgxEchartsDirective implements OnChanges, OnDestroy, OnInit, DoCheck, AfterViewInit {
  @Input() queryId: any;
  @Input() options: any;
  @Input() theme: string;
  @Input() loading: boolean;
  @Input() queryRelativeUrl: any;
  @Input() initOpts: {
    devicePixelRatio?: number
    renderer?: string
    width?: number | string
    height?: number | string
  };
  @Input() merge: any;
  @Input() autoResize = true;
  @Input() loadingType = 'default';
  @Input() loadingOpts: object;
  @Input() detectEventChanges = true; // deprecated, left for compatibility reasons to avoid triggering major version

  // ngx-echarts events
  @Output() chartInit = new EventEmitter<any>();

  // echarts mouse events
  @Output() chartClick = this.createLazyEvent('click');
  @Output() chartDblClick = this.createLazyEvent('dblclick');
  @Output() chartMouseDown = this.createLazyEvent('mousedown');
  @Output() chartMouseMove = this.createLazyEvent('mousemove');
  @Output() chartMouseUp = this.createLazyEvent('mouseup');
  @Output() chartMouseOver = this.createLazyEvent('mouseover');
  @Output() chartMouseOut = this.createLazyEvent('mouseout');
  @Output() chartGlobalOut = this.createLazyEvent('globalout');
  @Output() chartContextMenu = this.createLazyEvent('contextmenu');

  // echarts mouse events
  @Output() chartLegendSelectChanged = this.createLazyEvent('legendselectchanged');
  @Output() chartLegendSelected = this.createLazyEvent('legendselected');
  @Output() chartLegendUnselected = this.createLazyEvent('legendunselected');
  @Output() chartLegendScroll = this.createLazyEvent('legendscroll');
  @Output() chartDataZoom = this.createLazyEvent('datazoom');
  @Output() chartDataRangeSelected = this.createLazyEvent('datarangeselected');
  @Output() chartTimelineChanged = this.createLazyEvent('timelinechanged');
  @Output() chartTimelinePlayChanged = this.createLazyEvent('timelineplaychanged');
  @Output() chartRestore = this.createLazyEvent('restore');
  @Output() chartDataViewChanged = this.createLazyEvent('dataviewchanged');
  @Output() chartMagicTypeChanged = this.createLazyEvent('magictypechanged');
  @Output() chartPieSelectChanged = this.createLazyEvent('pieselectchanged');
  @Output() chartPieSelected = this.createLazyEvent('pieselected');
  @Output() chartPieUnselected = this.createLazyEvent('pieunselected');
  @Output() chartMapSelectChanged = this.createLazyEvent('mapselectchanged');
  @Output() chartMapSelected = this.createLazyEvent('mapselected');
  @Output() chartMapUnselected = this.createLazyEvent('mapunselected');
  @Output() chartAxisAreaSelected = this.createLazyEvent('axisareaselected');
  @Output() chartFocusNodeAdjacency = this.createLazyEvent('focusnodeadjacency');
  @Output() chartUnfocusNodeAdjacency = this.createLazyEvent('unfocusnodeadjacency');
  @Output() chartBrush = this.createLazyEvent('brush');
  @Output() chartBrushSelected = this.createLazyEvent('brushselected');
  @Output() chartRendered = this.createLazyEvent('rendered');
  @Output() chartFinished = this.createLazyEvent('finished');

  private chart: any;
  private currentOffsetWidth = 0;
  private currentOffsetHeight = 0;
  private currentWindowWidth: number;
  private resizeSub: Subscription;
  private visualMapTemp: any;
  private chartsData: any;

  constructor(
    private el: ElementRef,
    private ngZone: NgZone,
  ) {
  }

  ngOnChanges(changes: SimpleChanges) {
    const filter = ChangeFilter.of(changes);
    filter.notFirstAndEmpty<any>('options').subscribe(opt => this.onOptionsChange(opt));
    filter.notFirstAndEmpty<any>('merge').subscribe(opt => this.setOption(opt));
    filter.has<boolean>('loading').subscribe(v => this.toggleLoading(!!v));
    filter.notFirst<string>('theme').subscribe(() => this.refreshChart());
  }

  ngOnInit() {
    this.resizeSub = fromEvent(window, 'resize').pipe(debounceTime(50)).subscribe(() => {
      if (this.autoResize && window.innerWidth !== this.currentWindowWidth) {
        this.currentWindowWidth = window.innerWidth;
        this.currentOffsetWidth = this.el.nativeElement.offsetWidth;
        this.currentOffsetHeight = this.el.nativeElement.offsetHeight;
        this.resize();
      }
    });

  }

  ngOnDestroy() {
    // tslint:disable-next-line:no-unused-expression
    this.resizeSub && this.resizeSub.unsubscribe();
    this.dispose();
  }

  ngDoCheck() {
    // No heavy work in DoCheck!
    if (this.chart && this.autoResize) {
      const offsetWidth = this.el.nativeElement.offsetWidth;
      const offsetHeight = this.el.nativeElement.offsetHeight;

      if (this.currentOffsetWidth !== offsetWidth || this.currentOffsetHeight !== offsetHeight) {
        this.currentOffsetWidth = offsetWidth;
        this.currentOffsetHeight = offsetHeight;
        this.resize();
      }
    }
  }

  ngAfterViewInit() {
    setTimeout(() => this.initChart());
  }

  private dispose() {
    if (this.chart) {
      this.chart.dispose();
      this.chart = null;
    }
  }

  private resize() {
    if (this.chart) {
      this.chart.resize();
    }
  }

  private toggleLoading(loading: boolean) {
    if (this.chart) {
      loading ? this.chart.showLoading(this.loadingType, this.loadingOpts) : this.chart.hideLoading();
    }
  }

  // ä¿å­visualMap
  setOption(option: any, opts?: any) {
    this.chart.setOption(option, opts);
  }

  clearOption() {
    if (this.chart) {
      this.chart.clear();
    }
  }

  getOption(): any {
    return this.chart.getOption();
  }

  private refreshChart() {
    this.dispose();
    this.initChart();
  }

  private createChart() {
    this.currentWindowWidth = window.innerWidth;
    this.currentOffsetWidth = this.el.nativeElement.offsetWidth;
    this.currentOffsetHeight = this.el.nativeElement.offsetHeight;
    const dom = this.el.nativeElement;

    if (window && window.getComputedStyle) {
      const prop = window.getComputedStyle(dom, null).getPropertyValue('height');
      if ((!prop || prop === '0px') &&
        (!dom.style.height || dom.style.height === '0px')) {
        dom.style.height = '400px';
      }
    }

    return this.ngZone.runOutsideAngular(() => echarts.init(dom, this.theme, this.initOpts));
  }

  private initChart() {
    this.onOptionsChange(this.options);

    if (this.merge && this.chart) {
      this.setOption(this.merge);
    }
  }

  private onOptionsChange(opt: any) {
    if (opt) {
      if (!this.chart) {
        this.chart = this.createChart();
        this.chartInit.emit(this.chart);
      }

      this.chart.setOption(this.options, true);
    } else {
      if (!this.chart) {
        this.chart = this.createChart();
        this.chartInit.emit(this.chart);
      }
    }
  }

  // allows to lazily bind to only those events that are requested through the `@Output` by parent components
  // see https://stackoverflow.com/questions/51787972/optimal-reentering-the-ngzone-from-eventemitter-event for more info
  private createLazyEvent<T>(eventName: string): EventEmitter<T> {
    return this.chartInit.pipe(
      switchMap((chart: any) => new Observable(observer => {
        chart.on(eventName, (data: T) => this.ngZone.run(() => observer.next(data)));
        return null; // no need to react on unsubscribe as long as the `dispose()` is called in ngOnDestroy
      }))
    ) as EventEmitter<T>;
  }

  setNumber(data1: any, num: number): any {
    const arr = [];
    const l = data1.length;
    for (let i = 0; i < l; i++) {
      arr[i] = data1[i][num];
    }
    return arr;
  }

  setData(data: any, option?: any) {
    if (this.chart) {
      this.chart.clear();
    }
    data.forEach((item: any) => {
      if (item.dynamicPropSet) {
        const dy = item.dynamicPropSet;
        delete item.dynamicPropSet;
        Object.assign(item, dy);
      }
    });
    if (data && data.length) {
      this.chartsData = {
        dataset: {
          dimensions: [],
          source: []
        }
      };
      Object.keys(data[0]).forEach(key => {
        this.chartsData.dataset.dimensions.push(key);
      });
      data.forEach((item: any) => {
        const row: any[] = [];
        Object.keys(item).forEach(key => {
          row.push(item[key]);
        });
        this.chartsData.dataset.source.push(row);
      });
      if (option) {
        if (option.series && option.series.type && option.series.type === 'pie') {
          const resultOption = {
            title: option.title,
            dataset: this.chartsData.dataset,
            tooltip: {
              formatter: '{d}%',
            },
            series: [{
              type: option.series.type,
              encode: {
                value: option.series.encode.y[0],
                itemName: option.series.encode.x[0],
              },
            }]
          };
          resultOption.dataset.source.splice(0, 0, resultOption.dataset.dimensions);
          this.chart.setOption(resultOption);
        } else if (option.series && (option.series.type === 'line' || option.series.type === 'scatter')) {
          option.dataset = this.chartsData.dataset;
          this.setZoom(option);
          const row1 = this.chartsData.dataset.dimensions.indexOf(option.series.encode.y[0]);
          option.visualMap[0].dimension = row1;
          option.visualMap[0].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
          option.visualMap[0].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
          option.dataset.source.splice(0, 0, option.dataset.dimensions);
          this.chart.setOption(option);
        } else if (option.series && option.series.type && option.series.type === 'effectScatter') {
          option.dataset = this.chartsData.dataset;
          if (option.series.encode.value2.length === 0) {
            const row1 = this.chartsData.dataset.dimensions.indexOf(option.series.encode.value1[0]);
            option.visualMap[0].dimension = row1;
            option.visualMap[0].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
            option.visualMap[0].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
            const row2 = this.chartsData.dataset.dimensions.indexOf(option.series.encode.value1[0]);
            option.visualMap[1].dimension = row2;
            option.visualMap[1].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row2));
            option.visualMap[1].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row2));
          } else {
            const row = this.chartsData.dataset.dimensions.indexOf(option.series.encode.value1[0]);
            option.visualMap[0].dimension = row;
            option.visualMap[0].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row));
            option.visualMap[0].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row));

            const row2 = this.chartsData.dataset.dimensions.indexOf(option.series.encode.value2[0]);
            option.visualMap[1].dimension = row2;
            option.visualMap[1].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row2));
            option.visualMap[1].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row2));
          }
          option.dataset.source.splice(0, 0, option.dataset.dimensions);
          this.chart.setOption(option);
        } else if (option.series && option.series.type === 'bar') {
          if (option.series.encode.y && option.series.encode.y.length < 2) {
            const arrayTemp1: any = [];
            const arrayTemp2: any = [];
            // tslint:disable-next-line:prefer-for-of
            for (let i = 0; i < this.chartsData.dataset.source.length; i++) {
              arrayTemp1.push(this.chartsData.dataset.source[i][this.chartsData.dataset.dimensions.indexOf(option.series.encode.x[0])]);
              arrayTemp2.push(this.chartsData.dataset.source[i][this.chartsData.dataset.dimensions.indexOf(option.series.encode.y[0])]);
            }
            const isRepeat = Array.from(new Set(arrayTemp1)).length - arrayTemp1.length;
            if (isRepeat === 0) {
              option.dataset = this.chartsData.dataset;
              this.setZoom(option);
              const row1 = this.chartsData.dataset.dimensions.indexOf(option.series.encode.y[0]);
              option.legend = {};
              option.visualMap[0].dimension = row1;
              option.visualMap[0].min = Math.min.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
              option.visualMap[0].max = Math.max.apply(null, this.setNumber(this.chartsData.dataset.source, row1));
              option.dataset.source.splice(0, 0, option.dataset.dimensions);
              this.chart.setOption(option);
            }
            // åç»ï¼éæ°è®¾ç½®æ°æ®æ ¼å¼åå¤çé»è¾
            if (isRepeat < 0) {
              option.visualMap = [];
              option.dataset = this.chartsData.dataset;
              this.setZoom(option);
              const ob: any = [];
              for (let i = 0; i < arrayTemp1.length; i++) {
                ob.push(
                  {
                    name: arrayTemp1[i],
                    value: arrayTemp2[i]
                  });
              }
              const hash = {};
              let i = 0;
              const res = [];
              ob.forEach(function (item) {
                const name = item.name;
                hash[name] ? res[hash[name] - 1].value.push(item.value) : hash[name] = ++i && res.push({
                  name: name,
                  value: [item.value],
                });
              });
              // æ¿å°å¯¹è±¡æ°ç»è¿è¡è½¬ç½®
              const xdata: any = [];
              let max = 0;
              res.forEach(function (item) {
                xdata.push(item.name);
                if (item.value.length + 1 > max) {
                  max = item.value.length + 1;
                }
              });
              const series: any = [];
              const legendData = [];
              for (let i = 1; i < max; i++) {
                legendData.push(i + 'æ¬¡');
                series.push({
                  name: i + 'æ¬¡',
                  type: 'bar',
                  stack: 'æ»é',
                  data: [],
                });
              }
              res.forEach(function (item) {
                for (let i = 0; i < max - 1; i++) {
                  if (item.value[i]) {
                    series[i].data.push(item.value[i]);
                  } else {
                    series[i].data.push(0);
                  }
                }
              });

              option.xAxis = [{ data: xdata }];
              option.series = series;
              option.legend = { data: legendData };
              this.chart.setOption(option);
              return;
            }
          } else if (option.series.encode.y && option.series.encode.y.length > 1) {
            option.dataset = this.chartsData.dataset;
            this.setZoom(option);
            option.visualMap = [];
            const seriesChange = [];
            // legend data
            const yList = option.series.encode.y;
            for (let i = 0; i < yList.length; i++) {
              const temp: any = {
                type: option.series.type,
                name: option.legend.data[i],
                encode: {
                  x: option.series.encode.x,
                  y: [yList[i]],
                },
                label: {
                  normal: {
                    show: true,
                    position: 'insideTop'
                  }
                },
              };
              if (option.title.id && option.title.id === 'pile') {

                temp.stack = 'æ»é';
                delete temp.label;
              }
              seriesChange.push(temp);
            }
            option.series = seriesChange;
            option.dataset.source.splice(0, 0, option.dataset.dimensions);
            this.chart.setOption(option);
          }
        }

      }
    }
  }

  setZoom(op: any) {
    let dataZoom = [];
    if (op.dataset.source.length > 9) {
      dataZoom = [{
        id: 'dataZoomX',
        type: 'slider',
        xAxisIndex: [0],
        filterMode: 'filter'
      }];
      op.dataZoom = dataZoom;
    }
  }
}
